body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-attachment: fixed;
  background-image:url(/ReactPortfolio/static/media/abstract-astro-astronomy-956999-compressed.92409c99.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  height: 110vh;
}
html {
  height: 100vh;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container-wrapper{
  height: 100%!important;
  width: 100%!important;
}
h3{
  text-align: center;
  color:white;
  font-family: 'Bungee', cursive;
  font-size:3rem;
}
hr{
  background-color:white;
  height: 3px;
  width: 100%;
}
.column{
  color:white;
  font-size:7rem;
  text-align: center;
  height: 200px;
  padding:0;
}
i{
  position: relative;
  top:10px;
}
.column-contact{
  color:white;
  font-size:5rem;
  text-align: center;
  height: 150px;
}
.column-contact a i, .column-contact i{
  top:12px;
}
.column-about{
  color:white;
  font-size:7rem;
  text-align: center;
  height: 300px;
}
.column-about a i, .column-about i{
  top:60px;
}
.column-text{
  color:white;
  font-size:2rem;
  text-align: left;
  height: auto;
}
.column-text p{
  padding: 2rem;
}
.container{
  max-width: 600px;
  padding:0;
}
.column-portfolio{
  color:white;
  padding: 10px;
  min-height: 300px;
}
.column-portfolio img{
  margin: 0!important;
}
.column-portfolio .fa-github{
  text-align: right;
}
.column-item *{
  display: inline-block;
  margin: 0;
  padding: 0;
}
.column-item{
  padding: 1rem!important;
  color:white;
  text-align: center;
  height: auto;
}
.column-portfolio a{
  float:right;
}
.column-portfolio a i{
  top:0px;
}
.column-portfolio p{
  float:left;
}
.column-portfolio img{
  width: 100%;
  height: 80%;
  margin-top:5%;
}
.column-portfolio-arrow{
    color:white;
    font-size:7rem;
    text-align: center;
}
.column-portfolio-arrow i{
  top:60px;
}
img {
  object-fit: cover;
  width: 280px;
  height: 224px;
}
.fa-arrow-right:hover, .fa-arrow-left:hover, .fa-linkedin-in:hover, .fa-github:hover, .fa-envelope:hover{
  color:grey;
  -webkit-transition: color 200ms linear;
  transition: color 200ms linear;
}
.fa-arrow-left,.fa-arrow-right, .fa-linkedin-in, .fa-github, .fa-envelope{
  color:white;
  -webkit-transition: color 200ms linear;
  transition: color 200ms linear;
}
.container-page{
  position: absolute;
  margin-top:10vh;
  margin-bottom:10vh;
}
.row{
  margin:0;
}
.col{
  padding:0;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 500ms linear 500ms;
    transition: opacity 500ms linear 500ms;
}

/* exit */
.fade-exit {
    opacity: 1;
}
.fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 500ms linear;
    transition: opacity 500ms linear;
}
.fade-exit-done {
  opacity: 0;
}
.column-text{
  padding:10px;
}
@media screen and (min-width: 481px) and (max-width: 576px){
  .container{
    max-width: 400px;
  }
  h3{
    text-align: center;
    color:white;
    font-family: 'Bungee', cursive;
    font-size:2rem;
  }
  .column-text{
    color:white;
    font-size:1.5rem;
    text-align: left;
    height: auto;
  }
  .column-text p{
    padding: 1rem;
  }
  .column-contact{
    color:white;
    font-size:2.5rem;
    text-align: center;
    height: 100px;
  }
  .column-contact i {
    position: relative;
    top:20px;
  }
  .column-contact a i {
    position: relative;
    top:20px;
  }
  .column-portfolio{  
    height: 400px;
  }
  .column-portfolio-arrow{
    min-height: 200px;
    height:200px;
    padding: 10px;
  }
  .column-portfolio-arrow i{
    top: 2px;
  }
  .column-about{
    color:white;
    font-size:7rem;
    text-align: center;
    height: 200px;
  }
  .column-about a i, .column-about i{
    top:12px;
  }
}
@media screen and (max-width: 480px){
  .column{
    color:white;
    font-size:3.5rem;
    text-align: center;
    height: 150px;
  }
  .container{
    max-width: 300px;
  }
  h3{
    text-align: center;
    color:white;
    font-family: 'Bungee', cursive;
    font-size:1rem;
  }
  i{
    position: relative;
    top:30px;
  }
  .column-text{
    color:white;
    font-size:1rem;
    text-align: left;
    height: auto;
  }
  .column-text p{
    padding: 1rem;
  }
  .column-contact{
    color:white;
    font-size:1.5rem;
    text-align: center;
    height: 75px;
  }
  .column-contact i {
    position: relative;
    top:18px;
  }
  .column-contact a i {
    position: relative;
    top:18px;
  }
  .column-portfolio{
    height: 300px;
  }
  .column-portfolio-arrow{
    min-height: 150px;
    height:150px;
    padding: 10px;
  }
  .column-portfolio-arrow i{
    top: -35px;
    font-size:5rem;
  }
  .column-about{
    color:white;
    font-size:5rem;
    text-align: center;
    height: 150px;
  }
  .column-about a i, .column-about i{
    top:12px;
  }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

